    0px 1px 1px 0px rgba(0, 0, 0, 0.14), 0px 2px 1px -1px rgba(0, 0, 0, 0.12);
} */

.movieCast-image {
  width: 50%;
  /* height: 100px; */
  object-fit: cover;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.movieCast--item--name {
  text-align: center;
  font-weight: 500;
}
/* movieReviews */
.movieReviews--list {
  list-style: none;
  padding: 0;
}

.movieReviews--list--name {
  font-weight: 500;
}

.movieReviews--list--text {
  width: 800px;
}
